import React, { Component } from 'react';
import Select from './Select';
import './App.styl';

const detectMobile = () => {
  return window.innerWidth <= 768;
};

class App extends Component {
  constructor(props) {
    super(props);
    // TODO replace with redux
    this.state = { value: '' };
    this._onChange = this._onChange.bind(this);
  }

  // TODO replace with redux
  _onChange(e) {
    this.setState({ value: typeof e === 'string' ? e : e.target.value });
  }

  render() {
    return (
      <div className="App">
        <Select
          id="select-component"
          native={detectMobile()}
          value={this.state.value}
          onChange={this._onChange}
          placeholder="   "
          dataSet={[
            { value: '1', label: '' },
            { value: '2', label: '' },
            { value: '3', label: '' },
            { value: '4', label: '' },
            { value: '5', label: '' },
            { value: '6', label: '' },
            { value: '7', label: '' },
            { value: '8', label: '' },
            { value: '9', label: '' },
            { value: '10', label: '' },
          ]}
        />
      </div>
    );
  }
}

export default App;



